  padding-left: 2px;
  padding-right: 3rem;
  text-align: left;
  overflow-y: scroll;
}

.portal-vendor-info-form {
  display: grid;
  grid-gap: 1rem 3rem;
  grid-template-columns: 2fr 3fr;
  grid-template-areas: "name hours"
                       "tags hours"
                       "description hours"
                       "address phone"
                       "contact email";
}

#portal-settings-form-name-section {
  grid-area: name;
}

#portal-settings-form-tags-section {
  grid-area: tags;
}

#portal-settings-form-description-section {
  grid-area: description;
}

#portal-settings-form-hours-section {
  grid-area: hours;
}

#portal-settings-form-hours-section div {
  color: #afb0b1;
  margin: 1rem 0rem;
  display: grid;
  grid-template-columns: 1fr 5fr;
}

#portal-settings-form-hours-section div span {
  color: #8d8e8e;
  font-size: 1.5rem;
  font-weight: 400;
  display: inline-block;
}

#portal-settings-form-hours-section div span:nth-of-type(2) {
  text-align: right;
}

#portal-settings-form-hours-section input.number-input {
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
  outline: none;
  border: none;
  color: #979797;
  width: 1.5rem;
  height: 2.375rem;
  margin: 0rem 0.25rem;
  padding: 0rem 0.25rem;
  font-family: Roboto;
  font-size: 1.25rem;
}

#portal-settings-form-hours-section .am-pm-radio {
  margin: 0rem 0.25rem;
  display: inline-block;
  vertical-align: middle;
}

#portal-settings-form-hours-section span.hyphen {
  margin: 0rem 1rem;
}

#portal-settings-form-hours-section .am-pm-radio-option {
  color: #cdcdcd;
  font-size: 1rem;
  display: block;
  cursor: pointer;
  font-weight: 900;
}

#portal-settings-form-hours-section .am-pm-radio-option.active {
  color: #0082ff;
}

#portal-settings-form-address-section {
  grid-area: address;
}

#portal-settings-form-phone-section {
  grid-area: phone;
}

#portal-settings-form-contact-section {
  grid-area: contact;
}

#portal-settings-form-email-section {
  grid-area: email;
}

#portal-settings-form-submit-section button.orange-text {
  margin: 3rem 2rem;
}
